* {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

html {
  font-family: sans-serif;
  color: white;
}

/*-----------*/
/* NAVIGATION */
/*-----------*/
nav {
  height: 10vh;
  width: 100vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10%;
  background-color: rgba(0,0,0, .25);
}

nav a{
  color: white;
  text-decoration: none;
  font-size: 2vh;
  transition: ease-in-out .2s;
}

nav a:hover {
  color: rgba(255,255,255,0.8);
}

nav div {
  display: flex;
  justify-content: space-between;
  width: 30%;
}

.active {
  text-decoration: underline;
}

/*-----------*/
/* NAVIGATION */
/*-----------*/

body {
  margin: 0;
  overflow-x: hidden;
  background: linear-gradient(237.83deg, #357CE8 -10.66%, #104AA1 103.57%, #F22854 103.57%);
}

main {
  display: flex;
  flex-direction: column;
  width: 80ww;
  max-width: 1280px;
}

/*--------------*/
/* FIRST FRAME */
/*------------*/

#hero {
  flex-direction: row;
}

.frame {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.short {
  height: 90vh;
}

.value-prop > h1 {
  font-size: 5vh;
}

.value-prop > p {
  font-size: 2vh;
}

.checklist {
  box-shadow: 0px 0px 20px 10px rgba(0,0,0, .25);
  border-radius: 10px;
  width: 15%;
  min-width: 300px;
  height: 40%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: white;
  color: black;
}

.check {
  display: flex;
  align-items: center;
  margin: 1vh;
  width: 100%;
  margin-left: 4vw;
}

.checkbox {
  height: 20px;
  width: 20px;
  border: 2px solid black;
  border-radius: 3px;
  margin: 0 2%;
}

.checklist > span {
  color: grey;
  margin-top: 10%;
}

.primary {
  background-color: white;
  color: black;
  height: 40px;
  border: none;
  border-radius: 5px;
  flex-grow: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2vh;
  box-shadow:0px 0px 10px 5px rgba(0,0,0, .25);
  transition: ease-in-out .2s;
  text-decoration: none;
}

.primary:hover {
  cursor: pointer;
  box-shadow: 0px 0px 1px 0.5px rgba(0,0,0, .25);
}

.qa{
  width: 50vw;
}

.section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.download-section {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50vw;
  margin: 2vh 0;
}

.image-box {
  display: flex;
  align-items: center;
  width: 20vw;
  margin-right: 5vw;
}

.image-box > img{
  height: 10vh;
}

.image-box > span {
  font-size: 2vh;
}

.large {
  width: 30vw;
}

.alert {
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50vw;
  height: 5vh;
  background-color: #8C56FA;
  border-radius: 5px;
}

a {
  color: white;
}

.secondary {
  color: white;
  height: 40px;
  font-size: 2vh;
  text-decoration: none;
  text-align: center;
}

.arrow {
  height: 3vh;
  color: white;
  fill: white;
}

.continue {
  position: relative;
  bottom: 0px;
  display: flex;
  flex-direction: column;
  transition: ease-in-out .2s;
}

.continue:hover {
  bottom: -1%;
}